import React, { useState, useContext } from "react";
import { GoPencil, GoX, GoClippy } from "react-icons/go";
import { Alumno } from "./Alumno";
import { useHistory } from "react-router-dom";
import * as alumnoService from "./AlumnoService";
import moment from "moment";
import { confirmAlert } from "react-confirm-alert"; // Import
import { GiCardPlay, GiCalendar } from "react-icons/gi";
import { GoCheck, GoDash } from "react-icons/go";
import { UserContext } from "../Context/UserContext";
import { GrDocumentPdf } from "react-icons/gr";
import jsPDF from "jspdf";
import "jspdf-autotable";
import * as pagosService from "../Pagos/PagosService";
import { numberFormat } from "../lib/index";
import * as documentosService from "../Documentos/DocumentosService";
import * as notasService from "../Notas/NotasService";
import * as asistenciaService from "../Asistencia/AsistenciaService";
import * as practicasService from "../HorarioPracticas/PracticasService";
import { GrNotes } from "react-icons/gr";

import "moment/locale/es"; // without this line it didn't work

moment.locale("es");

interface Props {
  alumno: Alumno;
  loadAlumnos: () => void;
}
const SedesItem = (props: Props) => {
  const { userData }: any = useContext(UserContext);
  const [isLoading, setLoading] = useState<Boolean>(false);
  const { alumno, loadAlumnos }: any = props;
  const history = useHistory();

  const generatePDF = async (id: string) => {
    setLoading(true);
    const res: any = await alumnoService.getAlumno(id);
    const resPago: any = await pagosService.getPagoXAlumno(id);
    const resDoc: any = await documentosService.getDocumentos(id);
    const resNot: any = await notasService.getNotasXAlumnos(id);
    const resNot2: any = await notasService.getNotasXAlumnosS(id);
    const resAsis: any = await asistenciaService.getAsistenciaXAlumno(id);
    const resPra: any = await practicasService.getPracticasXAlumno(id);

    //console.log(resAsis.data);
    //console.log(resPra.data);
    const doc: any = new jsPDF();

    let logo =
      "data:image/png;base64,";
    doc.setFontSize(12);
    doc.setFont("courier", "bold");
    doc.text("ESCUELA DE CONDUCTORES INTEGRALES", 20, 10);
    doc.addImage(logo, "JPG", 10, 13, 100, 15);
    doc.line(200, 7, 120, 7);
    doc.setDrawColor(0);
    doc.setFillColor(255, 255, 0);
    doc.rect(120, 7, 80, 6, "FD");
    doc.text("MES", 155, 11);
    doc.setTextColor(0);
    doc.text(
      moment(String(res.data.createdAt)).format("MMMM").toUpperCase(),
      153,
      17
    );
    doc.setFillColor(0, 0, 0);
    doc.line(120, 7, 120, 30);
    doc.setDrawColor(0);
    doc.setFillColor(255, 255, 0);
    doc.rect(120, 18, 80, 6, "FD");
    doc.text("NRO", 155, 22);
    doc.setTextColor(0);
    doc.text(
      String(res.data.nro < 10 ? "0" + res.data.nro : res.data.nro),
      155,
      29
    );
    //doc.setTextColor(0);
    doc.line(200, 30, 120, 30);
    doc.line(200, 7, 200, 30);
    doc.setDrawColor(0);
    doc.setFillColor(255, 255, 0);
    doc.rect(10, 30, 100, 10, "FD");
    doc.text("FICHA DE MATRICULA", 40, 36);
    doc.setFillColor(0, 0, 0);
    doc.rect(10, 45, 190, 60);
    doc.setTextColor(0);
    doc.text("SEDE: ", 12, 50);
    doc.setFont("times", "normal");
    doc.text(String(res.data.sedes[0].name).toUpperCase(), 40, 50);

    doc.setFont("courier", "bold");
    doc.text("NRO DE MATRICULA: ", 80, 50);
    doc.setFont("times", "normal");
    doc.text(
      String(res.data.nro < 10 ? "0" + res.data.nro : res.data.nro),
      125,
      50
    );

    doc.line(70, 45, 70, 56);
    doc.line(138, 45, 138, 56);
    doc.setFont("courier", "bold");
    doc.text("FECHA: ", 145, 50);
    doc.setFont("times", "normal");
    doc.text(moment(String(res.data.createdAt)).format("DD/MM/YYYY"), 160, 50);
    doc.line(10, 56, 200, 56);
    doc.setFont("courier", "bold");
    doc.text("NOMBRES Y APELLIDOS: ", 12, 60);
    doc.setFont("times", "normal");
    var nombres = doc.splitTextToSize(String(res.data.nombres), 80);
    doc.text(nombres, 65, 60);
    doc.setFont("courier", "bold");
    doc.text("DNI: ", 145, 60);
    doc.line(10, 66, 200, 66);
    doc.setFont("times", "normal");
    doc.text(res.data.dni, 160, 60);
    doc.setFont("courier", "bold");
    doc.text("DIRECCIÒN: ", 12, 70);
    doc.setFont("times", "normal");
    var direccion = doc.splitTextToSize(
      String(res.data.address ? res.data.address : "NO REGISTRADO"),
      150
    );
    doc.text(direccion, 40, 70);
    doc.line(10, 76, 200, 76);
    doc.setFont("courier", "bold");
    doc.text("TEL. FIJO: ", 12, 80);
    doc.setFont("times", "normal");
    doc.text(
      String(res.data.telephone ? res.data.telephone : "NO REGISTRADO"),
      40,
      80
    );
    doc.line(77, 76, 77, 86);
    doc.setFont("courier", "bold");
    doc.text("TRAMITE: ", 80, 80);
    doc.setFont("times", "normal");
    doc.text(String(res.data.tramites[0].name).toUpperCase(), 105, 80);
    doc.line(10, 86, 200, 86);
    doc.setFont("courier", "bold");
    doc.text("CELULAR: ", 12, 90);
    doc.setFont("times", "normal");
    doc.text(String(res.data.cellphone).toUpperCase(), 40, 90);
    doc.line(10, 96, 200, 96);
    doc.setFont("courier", "bold");
    doc.text("INICIO DE CLASES: ", 12, 100);
    doc.setFont("times", "normal");
    doc.text(
      moment(String(res.data.startClasses)).utc().format("DD/MM/YYYY"),
      65,
      100
    );
    doc.line(94, 96, 94, 105);
    doc.setFont("courier", "bold");
    doc.text("CESE DE CLASES: ", 105, 100);
    doc.setFont("times", "normal");
    doc.text(
      moment(String(res.data.endClasses)).utc().format("DD/MM/YYYY"),
      145,
      100
    );

    var pagos = [];
    var documentos = [];
    var notas = [];
    var notasS = [];
    var asistencias = [];
    var practicas = [];
    for (var p = 0; p < resPago.data.length; p++) {
      pagos.push([
        numberFormat(resPago.data[p].cantidad),
        String(resPago.data[p].nroRecibo),
        moment(resPago.data[p].createdAt).format("DD/MM/YYYY"),
        String(resPago.data[p].registrador.nombres),
      ]);
    }
    for (var d = 0; d < resDoc.data.length; d++) {
      documentos.push([
        moment(resDoc.data[d].fechaEntregaBalotario).utc().format("DD/MM/YYYY"),
        moment(resDoc.data[d].fechaEntregaCertificadoMedico)
          .utc()
          .format("DD/MM/YYYY"),
        moment(resDoc.data[d].fechaEntregaCertificadoCofipro)
          .utc()
          .format("DD/MM/YYYY"),
        moment(resDoc.data[d].fechaEntregaCarnet).utc().format("DD/MM/YYYY"),
        moment(resDoc.data[d].fechaEntregaGuiaManejo)
          .utc()
          .format("DD/MM/YYYY"),
        moment(resDoc.data[d].fechaEntregaActa).utc().format("DD/MM/YYYY"),
      ]);
    }
    for (var n = 0; n < resNot.data.length; n++) {
      notas.push([
        String(resNot.data[n].tipoNota),
        String(resNot.data[n].nota),
        moment(String(resNot.data[n].createdAt)).format("DD/MM/YYYY"),
      ]);
    }
    for (var nn = 0; nn < resNot2.data.length; nn++) {
      notasS.push([
        String(resNot.data[nn].tipoNota),
        String(resNot.data[nn].nota),
        moment(String(resNot.data[nn].createdAt)).format("DD/MM/YYYY"),
      ]);
    }
    for (var a = 0; a < resAsis.data.length; a++) {
      asistencias.push([
        moment(String(resAsis.data[a].ingreso)).utc().format("DD/MM/YYYY"),
        moment(String(resAsis.data[a].ingreso)).utc().format("hh:mm A"),
        moment(String(resAsis.data[a].salida)).utc().format("hh:mm A"),
        String(resAsis.data[a].estudiante[0].nombres),
        String(resAsis.data[a].capitulo).toUpperCase(),
      ]);
    }
    for (var pr = 0; pr < resPra.data.length; pr++) {
      practicas.push([
        String(resPra.data[pr].nro),
        moment(String(resPra.data[pr].fecha))
          .format("ddd DD/MM/YYYY")
          .toUpperCase(),
        String(resPra.data[pr].horaInicio),
        String(resPra.data[pr].horaSalida),
        String(resPra.data[pr].profesor[0].nombres),
        String(resPra.data[pr].estudiante[0].nombres),
      ]);
    }
    doc.autoTable({
      //theme: "grid",
      styles: { halign: "center" },
      columnStyles: {
        0: { fillColor: [0, 255, 0] },
      }, // Cells in first column centered and green
      tableWidth: 190,
      margin: { top: 110, left: 10 },
      body: pagos,
      columns: ["Pagos", "Nro Recibo", "Nro Fecha", "Secretaria"],
    });

    doc.autoTable({
      tableWidth: 190,
      margin: { left: 10 },
      columns: [
        "F.E Balotario",
        "F.E Cert. Médico",
        "F.E Cert. Cofipro",
        "F.E Carnet",
        "F.E Guia Manejo",
        "F.E Acta Compromiso",
      ],
      body: documentos,
    });
    doc.setFont("courier", "bold");
    doc.text("NOTAS TEORICAS: ", 10, 179);
    doc.autoTable({
      tableWidth: 90,
      startY: 180,
      margin: { left: 10, right: 107 },
      columns: ["Tipo - 1era", "Nota - 1era", "Fecha - 1era"],
      body: notas,
    });

    doc.autoTable({
      tableWidth: 90,
      startY: 180,
      margin: { left: 110 },
      columns: ["Tipo - 2da", "Nota - 2da", "Fecha - 2da"],
      body: notasS,
    });
    doc.text("CONTROL DE ASISTENCIA TEORIA Y LIBROS: ", 10, 239);
    doc.autoTable({
      tableWidth: 190,
      startY: 240,
      margin: { left: 10 },
      columns: ["Fecha", "Ingreso", "Salida", "Alumno", "Capitulo"],
      body: asistencias,
    });
    doc.addPage();
    doc.text("CONTROL DE PRACTICAS - HORARIO: ", 10, 10);

    doc.autoTable({
      tableWidth: 190,
      margin: { left: 10 },
      columns: [
        "Nro Practica",
        "Fecha",
        "Hora desde",
        "Hora hasta",
        "Secretaria",
        "Alumno",
      ],
      body: practicas,
    });
    let COD = res.data.nro < 10 ? "0" + res.data.nro : res.data.nro;
    doc.save("FICHA_MATRICULA_" + COD + "_" + res.data.nombres);
    setLoading(false);
  };

  const handleDes = async (id: string) => {
    try {
      await alumnoService.deleteAlumnosById(id);
      loadAlumnos();
    } catch (e) {
      console.log(e.request.status);
    }
  };

  const handleHab = async (id: string) => {
    try {
      await alumnoService.habAlumnosById(id);
      loadAlumnos();
    } catch (e) {
      console.log(e.request.status);
    }
  };

  const modalDeshabilitar = () => {
    confirmAlert({
      title: "Advertencia",
      message: "Esta seguro que desea deshabilitar al estudiante?",
      buttons: [
        {
          label: "Si",
          onClick: () => alumno._id && handleDes(alumno._id),
        },
        {
          label: "No",
          onClick: () => {},
        },
      ],
    });
  };

  const modalHabilitar = () => {
    confirmAlert({
      title: "Mensaje",
      message: "Esta seguro que desea habilitar al estudiante?",
      buttons: [
        {
          label: "Si",
          onClick: () => alumno._id && handleHab(alumno._id),
        },
        {
          label: "No",
          onClick: () => {},
        },
      ],
    });
  };

  return (
    <>
      <tr key={alumno._id}>
        <th>{alumno.username}</th>
        <td style={{ textAlign: "center" }}>{alumno.nro}</td>
        <td>{alumno.nombres}</td>
        <td>{alumno.sedes[0].name}</td>
        <td>{alumno.tramites[0].name}</td>
        <td>{alumno.dni}</td>

        <td style={{ textAlign: "center" }}>
          {moment(alumno.createdAt).format("DD/MM/YYYY")}
        </td>
        <td style={{ textAlign: "center" }}>
          {moment(alumno.updatedAt).format("DD/MM/YYYY")}
        </td>
        <td>
          {userData.role === "Profesor" ? (
            <>
              <GoClippy
                onClick={() => history.push(`/alumnos/info/${alumno._id}`)}
                style={{
                  cursor: "pointer",
                  fontSize: "17px",
                  marginLeft: "5px",
                  color: "green",
                }}
              />
              <GrNotes
                onClick={() => history.push(`/notasmanejo/${alumno._id}`)}
                style={{
                  cursor: "pointer",
                  fontSize: "17px",
                  marginLeft: "5px",
                  color: "green",
                }}
              />
            </>
          ) : (
            <>
              {isLoading ? (
                <>Generando PDF...</>
              ) : (
                <>
                  <GoPencil
                    onClick={() =>
                      history.push(`/alumnos/registro/${alumno._id}`)
                    }
                    style={{
                      cursor: "pointer",
                      fontSize: "17px",
                      marginLeft: "5px",
                    }}
                  />
                  {alumno.estado === 1 ? (
                    <>
                      <GoCheck
                        onClick={modalDeshabilitar}
                        style={{
                          cursor: "pointer",
                          fontSize: "17px",
                          marginLeft: "5px",
                          color: "green",
                        }}
                      />
                    </>
                  ) : (
                    <>
                      <GoDash
                        onClick={modalHabilitar}
                        style={{
                          cursor: "pointer",
                          fontSize: "17px",
                          marginLeft: "5px",
                          color: "red",
                        }}
                      />
                    </>
                  )}

                  <GoClippy
                    onClick={() => history.push(`/alumnos/info/${alumno._id}`)}
                    style={{
                      cursor: "pointer",
                      fontSize: "17px",
                      marginLeft: "5px",
                      color: "green",
                    }}
                  />

                  <GiCardPlay
                    onClick={() => history.push(`/alumnos/pagos/${alumno._id}`)}
                    style={{
                      cursor: "pointer",
                      fontSize: "17px",
                      marginLeft: "5px",
                      color: "#FFC300",
                    }}
                  />

                  <GiCalendar
                    onClick={() =>
                      history.push(`/alumnos/fecha/documentos/${alumno._id}`)
                    }
                    style={{
                      cursor: "pointer",
                      fontSize: "17px",
                      marginLeft: "5px",
                      color: "blue",
                    }}
                  />

                  <GrDocumentPdf
                    onClick={() => alumno._id && generatePDF(alumno._id)}
                    style={{
                      cursor: "pointer",
                      fontSize: "17px",
                      marginLeft: "5px",
                      color: "red",
                    }}
                  />
                  <GrNotes
                    onClick={() => history.push(`/notasmanejo/${alumno._id}`)}
                    style={{
                      cursor: "pointer",
                      fontSize: "17px",
                      marginLeft: "5px",
                      color: "green",
                    }}
                  />
                </>
              )}
            </>
          )}
        </td>
      </tr>
    </>
  );
};

export default SedesItem;
